<template>
	<div class="login">
		<RXnav></RXnav>
		<div class="login-box">
			<!-- 登陆界面提示 -->
			<div class="login-title">
				<p class="gotitle">欢迎回归!</p>
				<p class="goEng">Welcome your back!</p>
			</div>
			<!-- 账号密码 -->
			<div class="login-textBox">
				<van-cell-group>
					<van-field @focus="openKeyboard" v-model="userLoginInfo.phone" label="手机号 :" placeholder="请输入手机号码" />
					<van-field @focus="openPassKeyboard" v-model="userLoginInfo.password" :type="isText ? 'text':'password' " label="密码 :" placeholder="请输入密码"
					:right-icon="isText?'eye-o':'closed-eye'" @click-right-icon="toggleType" />
				</van-cell-group>
				<div class="noPass" @click="ForgePass">
					<span>忘记密码?</span>
				</div>

			</div>

			<!-- 登陆按钮 -->
			<div class="btns">
				<van-button color="#21b276" block round @click="login">登陆</van-button>
				<van-button class="register" color="#999" block round @click="openPopup">注册</van-button>
			</div>
			<!-- 注册列表 -->
			<van-popup v-model="isOpen" position="bottom" closeable close-icon="close">
				<h2 style="text-align: center;">注 册</h2>
				<div class="login-textBox" style="padding: 0 15px;">
					<van-cell-group>
						<van-field v-model="userRegisterInfo.phone"  label="手机号 :" placeholder="请输入手机号码" />

						<van-field v-model="userRegisterInfo.password" :type="isText ? 'text':'password' " label="密码 :" placeholder="请输入密码"
						:right-icon="isText?'eye-o':'closed-eye'" @click-right-icon="toggleType" />

						<van-field v-model="userRegisterInfo.nickName" label="昵称 :" placeholder="请输入昵称" />
					</van-cell-group>

				</div>
				<div class="btns">
					<van-button class="register" color="#2C7873" block round @click="verification">注 册</van-button>
				</div>
			</van-popup>
			<!-- 键盘 -->
			<van-number-keyboard 
			v-model="userLoginInfo.phone" :show="iskeyboard" theme="custom" extra-key="." close-button-text="完成" 
			@blur="openKeyboard" 
			/>
			<van-number-keyboard
			v-model="userLoginInfo.password" :show="isPasskeyboard" theme="custom" extra-key="." close-button-text="完成" 
			@blur="openPassKeyboard" 
			/>
		</div>
	</div>
</template>

<script>
	import '../assets/less/login.less'
	import RXnav from "../components/RXnav.vue"
	import {
		mapMutations
	} from "vuex"
	import {
		utils
	} from "../assets/utils/utils.js"
	export default {
		name: 'Login',
		components: {
			RXnav
		},
		data() {
			return {
				// 用户登陆信息
				userLoginInfo: {
					phone: '',
					password: ''
				},
				// 注册信息
				userRegisterInfo: {
					phone: '',
					password: '',
					nickName: ''
				},
				//弹出注册栏
				isOpen: false,
				//切换密码类型
				isText: false,
				//控制账号键盘
				iskeyboard:false,
				//控制密码键盘
				isPasskeyboard:false,
			}
		},
		created() {
			this.changeShopbagCount;
			this.changeIsLoadShopbagCount(false)
		},

		methods: {
			
			//打开键盘
			openKeyboard(){
				this.iskeyboard=!this.iskeyboard
			},
			openPassKeyboard(){
				this.isPasskeyboard=!this.isPasskeyboard
			},
			
			...mapMutations(["changeShopbagCount", "changeIsLoadShopbagCount"]),
			//忘记密码
			ForgePass() {
				this.$router.push({
					name: "ForgePass"
				})
			},
			// 密码明文切换
			toggleType() {
				this.isText = !this.isText

			},
			// 注册栏弹出
			openPopup() {
				this.isOpen = !this.isOpen
			},
			// 验证匹配函数并注册
			verification() {
				let obj = {
					phone: {
						value: this.userRegisterInfo.phone,
						reg: /^1[3-9]\d{9}$/,
						errorMsg: '手机号格式不正确'
					},
					password: {
						value: this.userRegisterInfo.password,
						reg: /^[A-Za-z]\w{5,15}$/,
						errorMsg: '密码支持字母数字_组合且首字符必须为字母'
					},
					nickName: {
						value: this.userRegisterInfo.nickName,
						reg: /^[u4e00-u9fa5A-Za-z0-9]{1,10}$/,
						errorMsg: '昵称支持汉字字母数字组合'
					}
				};
				if (!utils.validForm(obj)) {
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500

				});

				//请求方式
				this.axios({
					method: 'POST',
					url: '/register',
					//如果是post请求,参数需要挂载到data
					data: {
						nickName: this.userRegisterInfo.nickName,
						password: this.userRegisterInfo.password,
						phone: this.userRegisterInfo.phone
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					if (res.data.code == 100) {

						//如果请求成功关闭注册弹出层
						this.isOpen = false
						//清除用户注册信息
						for (let key in this.userRegisterInfo) {
							this.userRegisterInfo[key] = ''
						}
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//登陆事件
			login() {
				let obj = {
					phone: {
						value: this.userLoginInfo.phone,
						reg: /^1[3-9]\d{9}$/,
						errorMsg: '手机号格式不正确'
					},
					password: {
						value: this.userLoginInfo.password,
						reg: /^[A-Za-z]\w{5,15}$/,
						errorMsg: '密码支持字母数字_组合且首字符必须为字母'
					}

				};
				if (!utils.validForm(obj)) {
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});

				//请求方式
				this.axios({
					method: 'POST',
					url: '/login',
					//如果是post请求,参数需要挂载到data
					data: {
						phone: this.userLoginInfo.phone,
						password: this.userLoginInfo.password,
					}
				}).then(res => {
					//如果请求成功清掉提示
					this.$toast.clear();
					if (res.data.code == 200) {
						// console.log(res);
						this.$cookies.set("tokenString", res.data.token, "1d")
						//清除用户注册信息
						for (let key in this.userLoginInfo) {
							this.userLoginInfo[key] = ''
						}
						return this.$router.push({
							name: "Main"
						})

					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			}



		}
	}
</script>
